:root {
  --background: #ffffff;
  --foreground: #171717;
}
@font-face {
  font-family: "TuesdayNight";
  src: url("../public/fonts/TuesdayNight-Regular.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
}
.background {
  background-size: cover;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.5);
  background-blend-mode: multiply;
}
.slider-bg-1 {
  background-image: var(--image-slider-1);
}
.slider-bg-2 {
  background-image: var(--image-slider-2);
}
.slider-bg-3 {
  background-image: var(--image-slider-3);
}
.parallax {
  position: relative;
  background-image: var(--parallax-image);
  min-height: 400px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  backdrop-filter: blur(6px);
}
.parallax::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.9);
  z-index: 0;
}
.event-image {
  background-image: var(--event-image);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.font-width {
  font-weight: 100;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
}
